$(document).ready(function() {
    const $slider = $('.slider');
    const $dots = $('.dot');
    const $images = $slider.children('a');
    const imageCount = $images.length;
    let isDragging = false;
    let startPos = 0;
    let currentTranslate = 0;
    let prevTranslate = 0;
    let currentIndex = 0;
    let movedBy = 0; // Ïù¥Îèô Í±∞Î¶¨ Í∏∞Î°ù

    $slider.on('mousedown touchstart', function(event) {
        isDragging = true;
        startPos = getPositionX(event);
        $slider.addClass('grabbing');
        movedBy = 0; // Ïù¥Îèô Ï¥àÍ∏∞Ìôî
        event.preventDefault(); // Í∏∞Î≥∏ ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ Í∏∞Îä• ÎπÑÌôúÏÑ±Ìôî
    });

    $slider.on('mouseup mouseleave touchend', function(event) {
        isDragging = false;
        $slider.removeClass('grabbing');

        // Ïù¥Îèô Í±∞Î¶¨ Í≥ÑÏÇ∞
        const finalTranslate = currentTranslate - prevTranslate;
        movedBy = finalTranslate;

        // ÎìúÎûòÍ∑∏Í∞Ä ÎÑàÎ¨¥ ÏßßÏúºÎ©¥ ÌÅ¥Î¶≠ÏúºÎ°ú Í∞ÑÏ£º
        if (Math.abs(movedBy) < 10) {
            return; // ÌÅ¥Î¶≠ Ïù¥Î≤§Ìä∏Î°ú Ï≤òÎ¶¨ÌïòÏßÄ ÏïäÏùå
        }

        if (movedBy < -100) {
            currentIndex = (currentIndex + 1) % imageCount;
        } else if (movedBy > 100) {
            currentIndex = (currentIndex - 1 + imageCount) % imageCount;
        }

        setPositionByIndex(true);
        updateDots();
    });

    $slider.on('mousemove touchmove', function(event) {
        if (isDragging) {
            const currentPosition = getPositionX(event);
            currentTranslate = prevTranslate + currentPosition - startPos;
            setSliderPosition();
            event.preventDefault(); // Í∏∞Î≥∏ ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ Í∏∞Îä• ÎπÑÌôúÏÑ±Ìôî
        }
    });

    $images.on('click', function(event) {
        // Ïä§ÏôÄÏù¥ÌîÑÍ∞Ä Ïù¥Î£®Ïñ¥Ï°åÎã§Î©¥ ÌÅ¥Î¶≠ Ïù¥Î≤§Ìä∏ Î¨¥Ïãú
        if (Math.abs(movedBy) >= 10) {
            event.preventDefault(); // ÌÅ¥Î¶≠ Ïù¥Î≤§Ìä∏ Í∏∞Î≥∏ ÎèôÏûë Î∞©ÏßÄ
            return; // ÌÅ¥Î¶≠ Ïù¥Î≤§Ìä∏ Î¨¥Ïãú
        }

        const url = $(this).find('img').data('url'); // imgÏùò data-url Í∞ÄÏ†∏Ïò§Í∏∞
        window.location.href = url;
    });

    $dots.on('click', function() {
        currentIndex = $(this).index();
        setPositionByIndex(true);
        updateDots();
    });

    function getPositionX(event) {
        return event.type.includes('mouse') ? event.pageX : event.originalEvent.touches[0].clientX;
    }

    function setSliderPosition() {
        $slider.css('transform', `translateX(${currentTranslate}px)`);
    }

    function setPositionByIndex(animate) {
        if (animate) {
            $slider.css('transition', 'transform 0.3s ease-out');
        } else {
            $slider.css('transition', 'none');
        }
        currentTranslate = currentIndex * -$slider.width();
        prevTranslate = currentTranslate;
        setSliderPosition();
    }

    function updateDots() {
        $dots.each(function(index) {
            $(this).toggleClass('active', index === currentIndex);
        });
    }

    $slider.on('transitionend', function() {
        if (currentIndex === imageCount) {
            $slider.css('transition', 'none');
            currentIndex = 0;
            setPositionByIndex(false);
        } else if (currentIndex === -1) {
            $slider.css('transition', 'none');
            currentIndex = imageCount - 1;
            setPositionByIndex(false);
        }
    });

    updateDots();
});
